/**
 * Table cell generator
 */
.qubely-tcg-container {
  background: #ffffff; }
  .qubely-tcg-container:not(.qubely-tcg-toolbar) {
    padding: 30px 30px 20px; }
  .qubely-tcg-container .qubely-tcg-row {
    display: flex; }
    .qubely-tcg-container .qubely-tcg-row span {
      flex-grow: 1;
      background: #f2f3f7;
      border: 1px solid #ced6e0;
      border-radius: 3px;
      margin: 2.5px;
      min-height: 30px;
      font-size: 12px;
      text-align: center;
      line-height: 28px; }
      .qubely-tcg-container .qubely-tcg-row span:hover, .qubely-tcg-container .qubely-tcg-row span.active {
        background: #d2e7ff;
        border-color: #a9d0ff; }
  .qubely-tcg-container .qubely-tcg-info {
    font-size: 14px;
    text-align: center;
    margin-top: 10px; }

.qubely-block-table .qubely-table-figure table {
  border-collapse: collapse; }

.qubely-block-table .qubely-table-figure .qubely-table.no-border td,
.qubely-block-table .qubely-table-figure .qubely-table.no-border th {
  border: none; }

.qubely-block-table .qubely-table-figure .qubely-table.no-outer-border {
  border: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr:first-child th {
    border-top: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr:first-child td {
    border-top: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr:last-child td {
    border-bottom: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr td:first-child {
    border-left: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr td:last-child {
    border-right: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr th:first-child {
    border-left: none; }
  .qubely-block-table .qubely-table-figure .qubely-table.no-outer-border tr th:last-child {
    border-right: none; }

.qubely-block-table .qubely-table-figure .qubely-table.no-horizontal-border td,
.qubely-block-table .qubely-table-figure .qubely-table.no-horizontal-border th {
  border-top: none !important;
  border-bottom: none !important; }

.qubely-block-table .qubely-table-figure .qubely-table.no-vertical-border td,
.qubely-block-table .qubely-table-figure .qubely-table.no-vertical-border th {
  border-left: none !important;
  border-right: none !important; }

.qubely-block-table table td,
.qubely-block-table table th {
  border: 1px inset #ddd; }
  .qubely-block-table table td.is-qubely-active,
  .qubely-block-table table th.is-qubely-active {
    border: 1px solid #2184f9;
    background-color: #d2e7ff; }

.qubely-block-table table .qubely-block-table_cell-content ul {
  margin: 0;
  padding: 0; }

.qubely-block-table table .qubely-block-table_cell-content.cell-placeholder {
  opacity: 0.62; }

.qubely-block-table table .qubely-block-table_cell-content.align-left {
  text-align: left; }

.qubely-block-table table .qubely-block-table_cell-content.align-center {
  text-align: center; }

.qubely-block-table table .qubely-block-table_cell-content.align-right {
  text-align: right; }

.qubely-block-table table .qubely-block-table_cell-content > div {
  display: inline-block; }

.qubely-block-table table .qubely-block-table_cell-content.hidden-cell {
  display: none; }

.qubely-block-table.fixed-width table {
  table-layout: fixed;
  white-space: nowrap; }

.image-cell.image-wrapper {
  max-width: 100%; }
  .image-cell.image-wrapper .table-cell-image-resizer,
  .image-cell.image-wrapper .qubely-image {
    display: inline-block;
    -webkit-transition: ease-in 300ms;
    transition: ease-in 300ms;
    object-fit: cover; }
  .image-cell.image-wrapper .components-placeholder.components-placeholder {
    min-height: 100px;
    z-index: 1; }
    .image-cell.image-wrapper .components-placeholder.components-placeholder .components-placeholder__fieldset {
      flex-direction: row;
      align-items: center;
      justify-content: center; }

.list-cell.list-wrapper .list-item {
  display: inline-block; }
  .list-cell.list-wrapper .list-item > li {
    text-align: left; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.custom-style > li {
    list-style: none; }
    .list-cell.list-wrapper .list-item.qubely-unordered-list.custom-style > li:before {
      font-family: "Font Awesome 5 Free";
      font-weight: 900;
      transition: color 400ms, background-color 400ms; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-check > li::before {
    content: "\f00c"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-check-square > li::before {
    content: "\f14a"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-check-square-outline > li::before {
    content: "\f14a";
    font-weight: 400; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-check-double > li::before {
    content: "\f560"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-check-circle > li::before {
    content: "\f058"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-check-circle-outline > li::before {
    content: "\f058";
    font-weight: 400; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-square > li::before {
    content: "\f0c8"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-square-outline > li::before {
    content: "\f0c8";
    font-weight: 400; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-circle > li::before {
    content: "\f111"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-circle-outline > li::before {
    content: "\f111";
    font-weight: 400; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-arrow-right > li::before {
    content: "\f061"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-arrow-left > li::before {
    content: "\f060"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-arrow-circle-right > li::before {
    content: "\f0a9"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-arrow-circle-left > li::before {
    content: "\f0a8"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-arrow-alt-circle-right > li::before {
    content: "\f35a";
    font-weight: 400; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-arrow-alt-circle-left > li::before {
    content: "\f359";
    font-weight: 400; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-long-arrow-alt-right > li::before {
    content: "\f30b"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-long-arrow-alt-left > li::before {
    content: "\f30a"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-chevron-right > li::before {
    content: "\f054"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-chevron-left > li::before {
    content: "\f053"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-angle-right > li::before {
    content: "\f105"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-angle-left > li::before {
    content: "\f104"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-star > li::before {
    content: "\f005"; }
  .list-cell.list-wrapper .list-item.qubely-unordered-list.qubely-list-bullet-star-outline > li::before {
    content: "\f005";
    font-weight: 400; }

/**
 * Editor css
 */
.qubely-table-cell-edit {
  position: relative;
  padding: 0px; }
  .qubely-table-cell-edit .qubely-tc-type-changer-wrap {
    position: absolute;
    top: 10px;
    transform: translateY(-50%);
    right: 5px;
    z-index: 2; }
    .qubely-table-cell-edit .qubely-tc-type-changer-wrap > button {
      cursor: pointer;
      padding: 0px;
      border: 1px solid #d6d9dd;
      width: 16px;
      height: 16px;
      text-align: center;
      color: #9b9b9b; }
      .qubely-table-cell-edit .qubely-tc-type-changer-wrap > button:hover {
        -webkit-box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
        box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12); }
      .qubely-table-cell-edit .qubely-tc-type-changer-wrap > button span {
        font-size: 12px;
        line-height: 10px;
        display: block; }
        .qubely-table-cell-edit .qubely-tc-type-changer-wrap > button span:hover {
          color: #2184f9; }
  .qubely-table-cell-edit .block-editor-rich-text__editable [data-rich-text-placeholder] {
    font-size: inherit;
    font-weight: inherit;
    word-spacing: 5px;
    opacity: 0.5; }

thead .qubely-table-cell-edit .block-editor-rich-text__editable [data-rich-text-placeholder] {
  opacity: 0.8; }

tfoot .qubely-table-cell-edit .block-editor-rich-text__editable [data-rich-text-placeholder] {
  opacity: 0.7; }

.qubely-block-table .qubely-table-cell-edit:last-child .qubely-tc-type-changer-wrap .qubely-tc-type-changer {
  left: auto;
  right: 0; }

.table-ratings {
  cursor: pointer;
  unicode-bidi: bidi-override;
  display: inline-block;
  position: relative;
  letter-spacing: 5px; }
  .table-ratings:before, .table-ratings:after {
    content: "\f005\f005\f005\f005\f005";
    font-family: "Font Awesome\ 5 Free";
    font-weight: 400;
    display: block;
    white-space: nowrap;
    z-index: 0; }
  .table-ratings:before {
    position: absolute;
    z-index: 1;
    left: 0px;
    overflow: hidden;
    font-family: "Font Awesome\ 5 Free";
    font-weight: 900;
    content: "\f005\f005\f005\f005\f005";
    width: var(--qubely-rating); }

.qubely-table-ratings-popover .components-popover__content {
  padding: 20px;
  min-width: 280px;
  border-radius: 5px; }

.qubely-action-change-listype {
  box-shadow: none; }
  .qubely-action-change-listype.is-active {
    color: #fff;
    background: #555d66; }

.components-popover.qubely-table-editor .components-dropdown-menu__menu button span.fas {
  margin-right: 7px; }

.qubely-block-table.fixed-width table .qubely-block-table_cell-content {
  white-space: pre-wrap;
  word-break: break-word; }

.qubely-tc-type-changer {
  background: white;
  width: 370px;
  display: grid;
  grid-template-columns: calc(50% - 10px) 50%;
  padding: 20px;
  border: 1px solid #dbdfe4;
  grid-gap: 10px; }
  .qubely-tc-type-changer button {
    height: 48px;
    font-size: 16px;
    line-height: 46px;
    padding: 0 12px;
    border: 1px solid #ced6e0;
    background: #f2f3f7;
    border-radius: 4px;
    text-align: left;
    color: #535f6a;
    cursor: pointer; }
    .qubely-tc-type-changer button i {
      margin-right: 10px;
      font-size: 16px;
      line-height: 46px; }
    .qubely-tc-type-changer button.active {
      background: #d2e7ff;
      border-color: #a9d0ff;
      color: #2184f9; }
    .qubely-tc-type-changer button:hover {
      border-color: #a9d0ff;
      -webkit-box-shadow: 0 5px 5px -3px rgba(119, 208, 255, 0.2), 0 8px 10px 1px rgba(119, 208, 255, 0.14), 0 3px 14px 2px rgba(119, 208, 255, 0.12);
      box-shadow: 0 5px 5px -3px rgba(119, 208, 255, 0.2), 0 8px 10px 1px rgba(119, 208, 255, 0.14), 0 3px 14px 2px rgba(119, 208, 255, 0.12); }

.qubely-table-button-url .qubely-field-url.qubely-field {
  padding: 10px;
  min-height: 100px; }

/* Table Responsive */
@media (max-width: 767px) {
  .qubely-block-table .qubely-table-figure {
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch; }
    .qubely-block-table .qubely-table-figure table {
      border-left-style: solid !important;
      border-right-style: solid !important; }
  .qubely-block-table:not(.fixed-width) table {
    table-layout: auto !important; }
    .qubely-block-table:not(.fixed-width) table td {
      white-space: nowrap; }
  .qubely-block-table.fixed-width table .qubely-block-table_cell-content {
    width: 100px; } }

.qubely-table-custom-typo .components-popover__content {
  padding: 20px;
  width: 300px;
  border-radius: 5px; }
