     * For Backward Compatibility
     */ }
    .qubely_woo_products_wrapper .qubely-product-ratings-wrapper .qubely-product-ratings:before, .qubely_woo_products_wrapper .qubely-product-ratings-wrapper .qubely-product-ratings:after {
      content: "\f005\f005\f005\f005\f005";
