    .qubely_woo_products_wrapper .qubely-product-ratings-wrapper .qubely-product-ratings[data-qubelyrating="3"]:before {
      width: 60%; }
    .qubely_woo_products_wrapper .qubely-product-ratings-wrapper .qubely-product-ratings[data-qubelyrating="3.5"]:before {
      width: 69%; }
    .qubely_woo_products_wrapper .qubely-product-ratings-wrapper .qubely-product-ratings[data-qubelyrating="4"]:before {
