.qubely-block-pie-progress {
  display: flex;
  flex-direction: column; }

.qubely-progress-parent {
  display: flex;
  position: relative; }
  .qubely-progress-parent .qubely-progress-inner-text {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    font-size: 16px;
    width: 90%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap; }
    .qubely-progress-parent .qubely-progress-inner-text > * {
      width: 100%; }
      .qubely-progress-parent .qubely-progress-inner-text > *:first-child {
        margin-top: auto; }
      .qubely-progress-parent .qubely-progress-inner-text > *:last-child {
        margin-bottom: auto; }
    .qubely-progress-parent .qubely-progress-inner-text .icon-image {
      width: 100%;
      align-items: center;
      justify-content: center;
      display: flex;
      flex-wrap: wrap; }
      .qubely-progress-parent .qubely-progress-inner-text .icon-image img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover; }
      .qubely-progress-parent .qubely-progress-inner-text .icon-image .qubely-pie-placeholder {
        border-radius: 4px;
        height: 60px;
        width: 80px;
        max-width: 90%;
        max-height: 90%;
        line-height: 60px;
        font-size: 30px;
        background: #dddddd;
        text-align: center;
        color: #666666; }

.qubely-pie-progress-heading {
  font-size: 20px;
  line-height: 1.1;
  width: 100%;
  letter-spacing: normal;
  text-transform: none; }

.qubely-progress-parent,
.qubely-pie-progress {
  max-width: 100%; }

.qubely-pie-progress svg {
  display: block; }
